.empty-show {
  background-color: #fafbfc;
}
.empty-show .empty-show-item {
  color: #807f83;
  padding: 10px;
  display: flex;
  align-items: center;
}
.empty-show-item i {
  font-size: 2.5em;
  margin: 4px;
}
.empty-show-item span {
  font-size: 14px;
}
@media only screen and (min-width: 992px) {
  .empty-show-item i {
    font-size: 3em;
  }
  .empty-show-item span {
    font-size: 20px;
  }
}
