.dashboard-parent,
.report-parent {
  background-color: #f5f5f5;
}
.dashboard-header,
.report-header {
  font-size: 32px;
  font-weight: 400;
  background-color: transparent !important;
  padding-top: 24px;
  padding-bottom: 10px;
  margin-bottom: 36px;
}
.dashboard-search,
.report-search {
  margin-bottom: 20px;
  -webkit-box-shadow: 0 1px 6px 0 rgba(0, 0, 0, 0.12),
    0 1px 6px 0 rgba(0, 0, 0, 0.12);
  box-shadow: 0 1px 6px 0 rgba(0, 0, 0, 0.12), 0 1px 6px 0 rgba(0, 0, 0, 0.12);
}

.dashboard-search-items:last-of-type,
.report-search-items:last-of-type {
  flex-grow: 0.24;
}
.dashboard-date-input,
.dashboard-date-input:hover,
.dashboard-date-input:active,
.dashboard-date-input:focus,
.report-date-input,
.report-date-input:hover,
.report-date-input:active,
.report-date-input:focus {
  font-size: 14px;
  width: 60px;
  text-align: center;
  margin: 0;
  border-bottom: none;
}
.dashboard-search-items,
.report-search-items {
  border-left: none;
  min-height: 55px;
  padding-top: 5px !important;
  padding-bottom: 5px !important;
}
.label-sum {
  color: #929292;
  font-size: 14px;
  font-weight: 600;
  margin-right: 5%;
  margin-left: 15px;
}
.total-sum {
  font-size: 24px;
  font-weight: 600;
  letter-spacing: 2px;
}
.panel {
  max-width: 828px;
  border-radius: 2px;
  border: 0;
  -webkit-box-shadow: 0 1px 6px 0 rgba(0, 0, 0, 0.12),
    0 1px 6px 0 rgba(0, 0, 0, 0.12);
  box-shadow: 0 1px 6px 0 rgba(0, 0, 0, 0.12), 0 1px 6px 0 rgba(0, 0, 0, 0.12);
}
.panel-header-item {
  border-top: solid 2px #fff;
  color: #222;
  width: 72px;
  color: #bbb;
  font-size: 14px;
  padding-top: 7px;
  text-align: center;
  cursor: pointer;
}
.panel-header-item-select {
  border-top: solid 2px #e53935;
  color: #222;
}

.notFoundDataForChart {
  font-size: 16px;
  color: #ddd;
  min-height: 150px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.miniNotFoundDataForChart {
  min-height: 150px;
  font-size: 16px;
  color: #ddd;
  display: flex;
  justify-content: center;
  align-items: center;
}

.miniPanel {
  -webkit-box-shadow: 0 1px 6px 0 rgba(0, 0, 0, 0.12),
    0 1px 6px 0 rgba(0, 0, 0, 0.12);
  box-shadow: 0 1px 6px 0 rgba(0, 0, 0, 0.12), 0 1px 6px 0 rgba(0, 0, 0, 0.12);
}

.reportSummary-panel {
  border: 0;
  -webkit-box-shadow: 0 1px 6px 0 rgba(0, 0, 0, 0.12),
    0 1px 6px 0 rgba(0, 0, 0, 0.12);
  box-shadow: 0 1px 6px 0 rgba(0, 0, 0, 0.12), 0 1px 6px 0 rgba(0, 0, 0, 0.12);
}
.reportSummary-panel-body {
  padding: 15px;
}
.reportSummary-panel-body-tabel {
  border-bottom: 2px solid #e6e6e6;
  border-top: 2px solid #e6e6e6;
}
table.showReportTable td {
  color: rgb(102, 102, 102);
}
table.reportProjectTabel thead td {
  color: #222 !important;
}
.project-access-thead th {
  border-top: 2px solid #ddd !important;
  border-bottom: 2px solid #ddd !important;
  font-size: 8px;
}
/* xsm */
@media only screen and (max-width: 575px) {
  table.showReportTable td {
    padding-right: 0.5rem !important;
    padding-left: 0.5rem !important;
    font-size: 13px;
  }
}
table.showReportTable thead td {
  border-top: 0;
  font-weight: bold;
  border-bottom: 2px solid #ddd;
}

table.showReportTable {
  background-color: #f5f5f5;
}
.reportProjectTabelBox {
  box-shadow: 0 1px 6px 0 rgba(0, 0, 0, 0.12), 0 1px 6px 0 rgba(0, 0, 0, 0.12);
}

@media only screen and (min-width: 768px) {
  .dashboard-search-items,
  .report-search-items {
    border-left: 1px solid #ddd;
    min-height: 55px;
    padding-top: 5px !important;
    padding-bottom: 5px !important;
  }
  .notFoundDataForChart {
    min-height: 250px;
    font-size: 22px;
  }
  .miniNotFoundDataForChart {
    min-height: 186px;
    font-size: 22px;
  }
  .miniPanel-Body {
    max-width: 828px;
    max-height: 438px;
  }
}
