.menu-parent {
  background-color: #323232;
}
.menu {
  width: 62px;
}
.menu-panel {
  padding: 15px;
}
.nav-logo {
  background-image: url("../asset/images/menu/nav-logo.png");
  background-repeat: no-repeat;
  width: 62px;
  height: 68px;
}
.list-group {
  border-radius: 0 !important;
}
.list-group-item {
  border: none;
  background-color: transparent !important;
  color: #cecece !important;
  height: 30px !important;
  font-size: 14px !important;
  margin-bottom: 2px !important;
  border-radius: 20px !important;
  padding: 0 8px !important;
  margin-top: 3px !important;
  text-decoration: none !important;
}
.nav-item i {
  font-size: 1.2em;
}
.nav-item i:hover,
.nav-item i:focus {
  color: #fff;
}
.nav-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
}
.menu-item-other > div:hover {
  color: #fff;
}

.menu-seperator {
  margin-bottom: 5px;
  margin-top: 10px;
  width: 60%;
  padding-right: 15px;
  min-height: 20px;
  border-bottom: solid 1px #cecece;
}
.menu-seperator span {
  color: #6f6f6f;
  font-size: 11px;
  font-weight: 700;
  opacity: 1;
}

.menu-item-active {
  background-color: #202020 !important;
  color: #fff !important;
}
.menu-item-dropdown-active {
  background-color: #4bc800 !important;
  color: #fff !important;
}
.menu-item-dropdown-other {
  color: #323232 !important;
}
.menu-item-dropdown-other:hover {
  background-color: #f5f5f5 !important;
}
.menu-reports-dropdown {
  position: relative;
}

.menu-reports-dropdown-content {
  display: none !important;
  position: absolute;
  right: 100%;
  top: 0;
  background-color: #fff;
  min-width: 160px;
  -webkit-box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.26);
  box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.26);
  z-index: 1;
  border-radius: 5px;
}

.menu-reports-dropdown:hover .menu-reports-dropdown-content {
  display: block !important;
}
@media only screen and (min-width: 992px) {
  .menu {
    width: 180px;
  }
  .menu-seperator {
    width: 100%;
    border-bottom: none;
  }
}
