.btn-project {
  width: 97px;
}
.projects-search {
  margin-bottom: 20px;
  -webkit-box-shadow: 0 1px 6px 0 rgba(0, 0, 0, 0.12),
    0 1px 6px 0 rgba(0, 0, 0, 0.12);
  box-shadow: 0 1px 6px 0 rgba(0, 0, 0, 0.12), 0 1px 6px 0 rgba(0, 0, 0, 0.12);
}

.projects-search-items:last-of-type {
  flex-grow: 0.24;
}

.btn-projects-search {
  background-color: #eee;
  border: 0;
  -webkit-box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14),
    0 3px 1px -2px rgba(0, 0, 0, 0.2), 0 1px 5px 0 rgba(0, 0, 0, 0.12);
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 3px 1px -2px rgba(0, 0, 0, 0.2),
    0 1px 5px 0 rgba(0, 0, 0, 0.12);
  color: rgba(0, 0, 0, 0.87);
  border-radius: 0;
}
.btn-projects-search:hover {
  background-color: #e4e4e4;
}
.btn-projects-search:active {
  -webkit-box-shadow: 0 4px 5px 0 rgba(0, 0, 0, 0.14),
    0 1px 10px 0 rgba(0, 0, 0, 0.12), 0 2px 4px -1px rgba(0, 0, 0, 0.2) !important;
  box-shadow: 0 4px 5px 0 rgba(0, 0, 0, 0.14), 0 1px 10px 0 rgba(0, 0, 0, 0.12),
    0 2px 4px -1px rgba(0, 0, 0, 0.2) !important;
  background-color: #e4e4e4;
}
.btn-projects-search:focus {
  -webkit-box-shadow: 0 0 8px rgba(0, 0, 0, 0.18),
    0 8px 16px rgba(0, 0, 0, 0.36);
  box-shadow: 0 0 8px rgba(0, 0, 0, 0.18), 0 8px 16px rgba(0, 0, 0, 0.36);
  background-color: #e4e4e4;
}
.color {
  width: 24px;
  height: 24px;
  border-radius: 2px;
}
.btn-color {
  box-shadow: 0 1px 3px rgba(128, 128, 128, 0.2);
  border-radius: 2px;
  padding: 3px 3px 3px 5px;
}
.btn-color-open {
  box-shadow: inset 0 3px 5px rgba(0, 0, 0, 0.125);
}
.colorPick-popover-body {
  width: 160px;
  height: 128px;
}
table.showProjects td {
  color: rgb(102, 102, 102);
}
.project-access-thead th {
  border-top: 2px solid #ddd !important;
  border-bottom: 2px solid #ddd !important;
  font-size: 8px;
}
/* xsm */
@media only screen and (max-width: 575px) {
  table.showProjects td {
    padding-right: 0.5rem !important;
    padding-left: 0.5rem !important;
    font-size: 13px;
  }
}
table.showProjects thead td {
  border-top: 0;
  font-weight: bold;
  border-bottom: 2px solid #ddd;
}

table.showProjects {
  background-color: #f5f5f5;
}
@media only screen and (min-width: 992px) {
  .projects-search-items {
    border-left: 1px solid #ddd;
  }
}
