.panel-parent {
  -webkit-box-shadow: 0 1px 6px 0 rgba(0, 0, 0, 0.12),
    0 1px 6px 0 rgba(0, 0, 0, 0.12);
  box-shadow: 0 1px 6px 0 rgba(0, 0, 0, 0.12), 0 1px 6px 0 rgba(0, 0, 0, 0.12);
  margin-bottom: 20px;
  min-height: 80px;
  background-color: #fff;
}
.panel-body {
  padding: 15px;
}
option.dropdown {
  display: hide;
}
.panel-tag i {
  font-size: 1.35em;
  color: #9e9e9e;
}
.panel-timer {
  margin-left: 15px;
  margin-right: 15px;
}
.panel-timer span {
  font-size: 30px !important;
}

.panel-player > i {
  font-size: 3.4em;
}
.panel-player-mode {
  border-right: solid 1px #ccc;
  min-height: 53px;
  min-width: 37px;
  padding: 0 10px;
}
.panel-player-mode i {
  font-size: 1.2em;
}
.player-mode-active {
  color: #4bc800;
}
.fa-play,
.fa-select {
  color: #4bc800;
}
.fa-play:hover,
.fa-select:hover {
  color: #43a047;
}
.fa-stop {
  color: #f44336;
}
.fa-stop:hover {
  color: #e53935;
}
.panel-list-time {
  font-size: 16px !important;
}

.projectUnvalidation {
  padding: 15px;
  margin: 0 0 18px 18px;
  box-shadow: -2px -1px 12px 0 rgba(100, 100, 100, 0.3),
    2px 1px 12px 0 rgba(100, 100, 100, 0.3);
  position: fixed;
  bottom: 0;
  left: 0;
  color: white;

  background-color: #f5685e;
  border-radius: 2px;
}
.projectUnvalidation:hover {
  box-shadow: -2px -1px 12px 0 rgba(0, 0, 0, 0.2),
    2px 1px 12px 0 rgba(0, 0, 0, 0.2);
  background-color: #f44336;
}
@media only screen and (min-width: 576px) {
  .projectUnvalidation {
    padding-left: 120px;
  }
}
@media only screen and (min-width: 992px) {
  .navCalculateAndModeBox {
    min-height: 70.5px;
  }
}
#Tags {
  border: none;
}
.available-tags-title {
  color: #777;
}
.tag-popover-parent span.arrow {
  display: none;
}
.tag-popover-parent .popover {
  border: 0;
  -webkit-box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.26);
  box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.26);
  border-radius: 0;
  width: 200px;
}
.selected-tags-badge {
  top: -13px;
  right: 40%;
  background-color: #f44336;
  color: #fff;
}
