.circle-color {
  border-radius: 50%;
  display: inline-block;
  margin: 0 5px 1px 10px;
  width: 7px;
  height: 7px;
}
.showOrder-box {
  color: #9e9e9e;
  background-color: #fff;
  border-bottom: 1px solid #e9e9e9;
}
.showOrder-box i {
  color: #9e9e9e;
}
.showOrder-box i:hover {
  color: #000;
}
.showOrder-item {
  border-top: 1px solid #e9e9e9;
}
.showOrder-title div span {
  font-size: 18px;
}
@media only screen and (min-width: 768px) {
  .col-title {
    min-width: 20%;
  }
  .col-project {
    min-width: 15%;
  }
  .col-activity {
    min-width: 15%;
  }
  .col-hour {
    min-width: 20%;
  }
  .col-count {
    min-width: 15%;
  }
  .col-btn {
    min-width: 15%;
  }
}
