* {
  direction: rtl;
  text-align: right;
  box-sizing: border-box;
  font-family: IRANSans;
  margin: 0;
  padding: 0;
  outline: none !important;
  font-size: 15px;
  background-color: transparent;
}
body {
  background-color: #fafbfc;
  height: 100%;
  color: #222;
  font-weight: 400;
}
i {
  cursor: pointer;
}
.pointer {
  cursor: pointer;
}
.iranSansNum {
  font-family: IRANSansNum;
}
.fs-12 {
  font-size: 12px;
}
.fs-13 {
  font-size: 13px;
}
.fs-14 {
  font-size: 14px;
}
.hidden {
  visibility: hidden;
}
.h-100vh {
  height: 100vh;
}
.w-20 {
  width: 20%;
}
.w-80 {
  width: 80%;
}
.w-71px {
  width: 71px;
}
.w-80-i {
  width: 80% !important;
}
.mw-70 {
  min-width: 70px;
}

.mt-120 {
  margin-top: 120px;
}
.mb-60 {
  margin-bottom: 60px;
}
.mb-120 {
  margin-bottom: 120px;
}
.mr-62 {
  margin-right: 62px;
}
.rotate-27 {
  transform: rotate(27deg);
}
.opacity-0 {
  opacity: 0;
}
.btn {
  border-radius: 0 !important;
  -webkit-box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14),
    0 3px 1px -2px rgba(0, 0, 0, 0.2), 0 1px 5px 0 rgba(0, 0, 0, 0.12);
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 3px 1px -2px rgba(0, 0, 0, 0.2),
    0 1px 5px 0 rgba(0, 0, 0, 0.12);
  color: rgba(255, 255, 255, 0.84);
  padding: 5px 20px;
  border: 0;
  font-weight: 500;
}
.btn-padding-orginal {
  padding: 0.375rem 0.75rem !important;
}
.btn:active {
  -webkit-box-shadow: 0 4px 5px 0 rgba(0, 0, 0, 0.14),
    0 1px 10px 0 rgba(0, 0, 0, 0.12), 0 2px 4px -1px rgba(0, 0, 0, 0.2) !important;
  box-shadow: 0 4px 5px 0 rgba(0, 0, 0, 0.14), 0 1px 10px 0 rgba(0, 0, 0, 0.12),
    0 2px 4px -1px rgba(0, 0, 0, 0.2) !important;
}
.btn:focus {
  -webkit-box-shadow: 0 0 8px rgba(0, 0, 0, 0.18),
    0 8px 16px rgba(0, 0, 0, 0.36);
  box-shadow: 0 0 8px rgba(0, 0, 0, 0.18), 0 8px 16px rgba(0, 0, 0, 0.36);
}
.btn-success {
  background-color: #4bc800 !important;
}
.btn-danger {
  background-color: #ff5722 !important;
  color: rgba(255, 255, 255, 0.84);
}
.btn-secondary {
  border: none;
  border-radius: 1px;
  background-color: #eee !important;
  color: rgba(0, 0, 0, 0.87) !important;
  font-weight: 500;
}
.btn-secondary:hover {
  border: none;
  background-color: #e4e4e4 !important;
}
.btn-primary {
  border: none;
  border-radius: 1px;
  background-color: #009688 !important;
  color: rgba(255, 255, 255, 0.84) !important;
  font-weight: 500;
}
.btn-primary:hover {
  border: none;
  background-color: #00aa9a !important;
}
.btn-primary:focus {
  background-color: #00aa9a !important;
}
.btn-danger:hover,
.btn-danger:active,
.btn-danger:focus {
  background-color: #ff6736 !important;
}
.input-label {
  font-size: 10px;
  color: #9e9e9e;
  width: 20%;
}
.form-control,
.form-control:hover,
.form-control:focus,
.form-control:active {
  font-size: 12px;
  border-radius: 0;
  margin-bottom: 7px;
  border: none;
  box-shadow: none !important;
  border-bottom: 1px solid #d2d2d2;
  height: 38px;
  padding: 7px 0;
  color: #555;
  line-height: 1.42857143;
  width: 80%;
}

.create-modal .modal-content {
  -webkit-box-shadow: 0 27px 24px 0 rgba(0, 0, 0, 0.2),
    0 40px 77px 0 rgba(0, 0, 0, 0.22);
  box-shadow: 0 27px 24px 0 rgba(0, 0, 0, 0.2),
    0 40px 77px 0 rgba(0, 0, 0, 0.22);
  border-radius: 2px;
  border: none;
  background-color: #f5f5f5;
}
.create-modal .modal-header {
  align-items: center;
  padding: 24px 30px !important;
  box-shadow: 0 1px 3px rgba(128, 128, 128, 0.2);
  -webkit-box-shadow: 0 1px 3px rgba(128, 128, 128, 0.2);
  -moz-box-shadow: 0 1px 3px rgba(128, 128, 128, 0.2);
  border-bottom: none;
}
.create-modal .modal-title {
  margin-left: auto;
}
.create-modal .modal-header > .close,
.create-modal .modal-header > .close:hover {
  color: #222;
  opacity: 1;
  margin: 0;
  padding: 0;
}
.create-modal .modal-header > .close span {
  font-size: 28px;
}
.create-modal .modal-content .modal-body {
  padding-top: 24px;
  padding-right: 24px;
  padding-bottom: 16px;
  padding-left: 24px;
}
.create-modal .form-control {
  background-color: #f5f5f5;
}
.create-modal .modal-footer {
  border-top: none;
}

.form-control::placeholder {
  font-size: 12px;
  line-height: 24px;
  color: #bdbdbd;
  font-weight: 400;
}
.input-label-unvalidation {
  color: #f23232;
}
.inputUnvalidation,
.inputUnvalidation:hover,
.inputUnvalidation:focus,
.inputUnvalidation:active {
  border-bottom: 1px solid #f23232;
  color: #f23232;
}

.inputUnvalidation::placeholder {
  color: #f23232;
}
.badge {
  border-radius: 1px;
  padding: 0.3em 0.6em;
}
.badge-circle {
  padding-right: 0.6em;
  padding-left: 0.6em;
  border-radius: 50%;
}
.badge-secondary {
  background-color: #9e9e9e;
}
.badge-success {
  background-color: #4bc800;
}
select.form-control {
  /* REVIEW property below hide our arrow icon in select/option */
  appearance: none;
  -moz-appearance: none;
  -webkit-appearance: none;
}
/* REVIEW hide for IE10 */
select.form-control::-ms-expand {
  display: none;
}

@keyframes fadeIn {
  0% {
    visibility: hidden;
    opacity: 0;
  }
  100% {
    visibility: visible;
    opacity: 1;
  }
}
@keyframes fadeOut {
  0% {
    visibility: visible;
    opacity: 1;
  }
  100% {
    visibility: hidden;
    opacity: 0;
  }
}

.fadeIn {
  animation: fadeIn 1s ease;
}
.fadeOut {
  animation: fadeOut 1s ease;
}
@media only screen and (min-width: 768px) {
  .w-md-50 {
    width: 50%;
  }
  .w-md-45-i {
    width: 45% !important;
  }
}
@media only screen and (min-width: 992px) {
  .form-control,
  .form-control:hover,
  .form-control:focus,
  .form-control:active {
    font-size: 14px;
    width: 100%;
  }

  .input-label {
    font-size: 12px;
    width: auto;
  }
}
